import {
  PageHeader,
  Card,
  Input,
  Form,
  Button,
  Select,
  message,
  InputNumber,
  DatePicker,
} from "antd";
import { ICollection, ICollectionSchema } from "../../types/global";
import { crudCollection, EOperations, ICurdBody } from "../../http";
import moment from "moment";
import RefSelect from "../DataFlow/RefSelect";
import UploadFile from "./UploadFile";
import JSONEditor from "./JSONEditor";

const { Option } = Select;

const getKeyValue =
  <U extends keyof T, T extends object>(key: U) =>
  (obj: T) =>
    obj[key];

function CreateCollectionAction({
  selectedCollection,
  action = "create",
  id,
  selectedRecord,
}: {
  selectedCollection: ICollection;
  action?: "create" | "update";
  id?: string;
  selectedRecord?: any;
}) {
  const schema: ICollectionSchema = selectedCollection?.schema;
  const [form] = Form.useForm();

  const onFinish = (values: any) => {
    createData(values);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  const createData = async (formData: any) => {
    try {
      const body: ICurdBody = {
        model: selectedCollection?.name || "",
        operations:
          action === "create" ? EOperations.create : EOperations.update,
        data: {
          body: formData,
          id: id,
        },
      };
      await crudCollection(body);
      message.success("Data Saved");
      window.history.back();
    } catch (error: any) {
      message.error(error.response.data);
      console.log(error);
    }
  };

  return (
    <div>
      <PageHeader
        onBack={() => window.history.back()}
        title={action === "create" ? "Create New" : "Edit"}
      />
      <Card
        style={{ marginLeft: 10, marginRight: 10 }}
        title={
          <span style={{ letterSpacing: 1.2, textTransform: "capitalize" }}>
            {selectedCollection?.name}
          </span>
        }
      >
        <Form
          name="basic"
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          layout="vertical"
          size="large"
          form={form}
        >
          {schema &&
            Object.keys(schema).map((key: any) => {
              const schemaValue = getKeyValue<
                keyof ICollectionSchema,
                ICollectionSchema
              >(key)(schema);

              switch (schemaValue.type) {
                case "string":
                  return (
                    <Form.Item
                      label={
                        <span
                          style={{
                            textTransform: "capitalize",
                            color: "#434343",
                          }}
                        >
                          {schemaValue.name}
                        </span>
                      }
                      name={schemaValue.name}
                      rules={[
                        {
                          required: schemaValue.required,
                          enum: ["male", "female"],
                        },
                      ]}
                      initialValue={
                        action === "update"
                          ? selectedRecord[schemaValue.name]
                          : ""
                      }
                    >
                      {schemaValue.enum.length ? (
                        <Select placeholder={`Select ${schemaValue.name}`}>
                          <>
                            {schemaValue.enum.map((e: any) => (
                              <Option value={e}>{e}</Option>
                            ))}
                          </>
                        </Select>
                      ) : (
                        <Input />
                      )}
                    </Form.Item>
                  );

                case "number":
                  return (
                    <Form.Item
                      label={
                        <span
                          style={{
                            textTransform: "capitalize",
                            color: "#434343",
                          }}
                        >
                          {schemaValue.name}
                        </span>
                      }
                      name={schemaValue.name}
                      rules={[
                        {
                          required: schemaValue.required,
                        },
                      ]}
                      initialValue={
                        action === "update"
                          ? selectedRecord[schemaValue.name]
                          : ""
                      }
                    >
                      <InputNumber style={{ width: 300 }} />
                    </Form.Item>
                  );
                case "date":
                  return (
                    <Form.Item
                      label={
                        <span
                          style={{
                            textTransform: "capitalize",
                            color: "#434343",
                          }}
                        >
                          {schemaValue.name}
                        </span>
                      }
                      name={schemaValue.name}
                      rules={[
                        {
                          required: schemaValue.required,
                        },
                      ]}
                      initialValue={
                        action === "update"
                          ? moment(selectedRecord[schemaValue.name])
                          : moment()
                      }
                    >
                      <DatePicker />
                    </Form.Item>
                  );
                case "boolean":
                  return (
                    <Form.Item
                      label={
                        <span
                          style={{
                            textTransform: "capitalize",
                            color: "#434343",
                          }}
                        >
                          {schemaValue.name}
                        </span>
                      }
                      name={schemaValue.name}
                      rules={[
                        {
                          required: schemaValue.required,
                        },
                      ]}
                      initialValue={
                        action === "update"
                          ? selectedRecord[schemaValue.name]?.toString()
                          : "false"
                      }
                    >
                      <Select placeholder={`Select`}>
                        <Option value={"true"}>True</Option>
                        <Option value={"false"}>False</Option>
                      </Select>
                    </Form.Item>
                  );
                case "ref":
                  return (
                    <RefSelect
                      action={action}
                      selectedRecord={selectedRecord}
                      schemaValue={schemaValue}
                    />
                  );
                case "file":
                  return (
                    <UploadFile
                      schemaValue={schemaValue}
                      form={form}
                      selectedRecord={selectedRecord}
                      action={action}
                    />
                  );
                case "json":
                  return (
                    <JSONEditor
                      action={action}
                      selectedRecord={selectedRecord}
                      schemaValue={schemaValue}
                      form={form}
                    />
                  );

                default:
                  return null;
              }
            })}

          <Form.Item>
            <Button type="primary" htmlType="submit">
              {action === "create" ? "Save" : "Update"}
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
}

export default CreateCollectionAction;
