import React, { useState } from "react";
import { Layout, Menu, Divider, Button, Space } from "antd";
import {
  AlignCenterOutlined,
  LineChartOutlined,
  DatabaseOutlined,
  FileOutlined,
  ApiTwoTone,
  ApiOutlined,
  UserSwitchOutlined,
  SlidersTwoTone,
  SlidersOutlined,
  CodeSandboxOutlined,
  CodeSandboxCircleFilled,
  PoweroffOutlined,
} from "@ant-design/icons";
import { Link, useLocation } from "react-router-dom";

const { Sider } = Layout;

function SideBar() {
  const location = useLocation();

  const [collapsed, setCollapsed] = useState(false);

  const selectedMenu = location.pathname.split("/")[1];

  const pages = [
    {
      key: "",
      to: "/",
      name: "Schema Flow",
      icon: <LineChartOutlined />,
    },
    {
      key: "collections",
      to: "/collections",
      name: "Collections",
      icon: <AlignCenterOutlined />,
    },
    {
      key: "data",
      to: "/data",
      name: "Data Flow",
      icon: <DatabaseOutlined />,
    },
  ];

  const plugins = [
    {
      key: "files",
      to: "/files",
      name: "Files",
      icon: <FileOutlined />,
    },
    {
      key: "auth",
      to: "/auth",
      name: "Auth",
      icon: <UserSwitchOutlined />,
    },
  ];

  return (
    <Sider
      collapsible={true}
      collapsedWidth="50"
      theme="light"
      collapsed={collapsed}
      onCollapse={(collapsed, type) => {
        setCollapsed(collapsed);
      }}
      style={{ height: "100vh" }}
    >
      <div className="logo-container">
        <img
          className={collapsed ? "logo-small" : "logo-large"}
          src={collapsed ? "/img/o-logo.png" : "/img/black-logo.png"}
          alt=""
        ></img>
      </div>
      <Menu
        style={{ marginTop: 10 }}
        mode="inline"
        defaultSelectedKeys={[selectedMenu]}
      >
        <Divider orientation="left">
          {collapsed ? (
            <CodeSandboxCircleFilled />
          ) : (
            <Space>
              <CodeSandboxOutlined /> Core
            </Space>
          )}
        </Divider>
        {pages.map((page) => (
          <Menu.Item key={page.key} icon={page.icon}>
            <Link to={page.to}>{page.name}</Link>
          </Menu.Item>
        ))}

        <Divider orientation="left">
          {collapsed ? (
            <ApiTwoTone />
          ) : (
            <Space>
              {" "}
              <ApiOutlined /> Plugins{" "}
            </Space>
          )}
        </Divider>
        {plugins.map((plugin) => (
          <Menu.Item key={plugin.key} icon={plugin.icon}>
            <Link to={plugin.to}>{plugin.name}</Link>
          </Menu.Item>
        ))}
      </Menu>

      <Divider orientation="left">
        {collapsed ? (
          <SlidersTwoTone />
        ) : (
          <Space>
            <SlidersOutlined /> General
          </Space>
        )}
      </Divider>

      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "start",
        }}
      >
        <Button
          type="link"
          onClick={() => {
            window
              .open("http://localhost:3002/admin/api-docs/", "_blank")
              .focus();
          }}
        >
          {collapsed ? <b>API</b> : "Api Documentation"}
        </Button>

        <Button
          type="link"
          onClick={() => {
            localStorage.clear();
            window.location.replace("/admin-ui");
          }}
        >
          {collapsed ? <PoweroffOutlined /> : "Logout"}
        </Button>
      </div>
    </Sider>
  );
}

export default SideBar;
