import { Dispatch, useCallback, useEffect, useState } from "react";
import {
  Table,
  PageHeader,
  Button,
  Tag,
  Modal,
  message,
  List,
  Image,
  Space,
  Menu,
  Dropdown,
} from "antd";
import { ICurdBody, crudCollection, EOperations } from "../http";
import { useParams, useHistory } from "react-router-dom";
import { PlusOutlined, FilterOutlined } from "@ant-design/icons";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { CollectionsState, ICollection } from "../types/global";
import queryString from "query-string";
import { selectCollection } from "../store/actionCreators";
import CreateCollectionAction from "../components/DataFlow/CreateCollectionAction";
import ShowCollectionAction from "../components/DataFlow/ShowCollectionAction";
import {
  EditOutlined,
  DeleteOutlined,
  ExclamationCircleOutlined,
  EyeOutlined,
  MoreOutlined,
} from "@ant-design/icons";

import moment from "moment";
import prettySize from "prettysize";

const { Column } = Table;
const { confirm } = Modal;
interface IParams {
  action?: string;
  id?: string;
}

function DataFlowPage() {
  const { collection: collectionName }: any = useParams();

  const history = useHistory();
  const params: IParams = queryString.parse(window.location.href.split("?")[1]);

  const [list, setList] = useState<Array<{ name: string; type: string }>>([]);
  const [columns, setColumns] = useState([]);

  const [selectedRecord, setSelectedRecord] = useState<any>({});

  const [loading, setLoading] = useState(true);
  const [rowSelection, setRowSelection] = useState<any>([]);
  const [pagination, setPagination] = useState<{
    current: number;
    pageSize: number;
    total?: number;
  }>({
    current: 1,
    pageSize: 10,
  });
  const dispatch: Dispatch<any> = useDispatch();

  const selectedCollection: ICollection | undefined = useSelector(
    (state: CollectionsState) => state.selectedCollection,
    shallowEqual
  );

  const setSelectedCollection = useCallback(
    (collection: ICollection) => dispatch(selectCollection(collection)),
    [dispatch]
  );

  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections,
    shallowEqual
  ).filter((collection) => collection.show);

  const getCollectionList = async (model: string, pagination: any) => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: model,
        operations: EOperations.list,
        data: {
          query: {
            pagination: pagination,
          },
        },
      };
      const { data } = await crudCollection(action);
      setPagination(data.pagination);
      setList(data.list);
      setColumns(data.column);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  const deleteRecord = async (id: string) => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.delete,
        data: {
          id: id,
        },
      };
      await crudCollection(action);
      message.success("Record Deleted");
      getCollectionList(collectionName, pagination);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  const deleteManyRecord = async () => {
    setLoading(true);
    try {
      const action: ICurdBody = {
        model: collectionName,
        operations: EOperations.deleteAll,
        data: {
          id: rowSelection,
        },
      };
      await crudCollection(action);
      message.success("Records Deleted");
      setRowSelection([]);
      getCollectionList(collectionName, pagination);
    } catch (error) {
      console.log(error);
    }
    setLoading(false);
  };

  useEffect(() => {
    if (collectionName && Object.keys(params).length === 0) {
      setColumns([]);
      setList([]);
      getCollectionList(collectionName, pagination);
    }
    if (!selectedCollection) {
      history.push("/data");
    }
  }, [collectionName, selectedCollection, window.location.href]);

  const dataSource = list
    ? list.map((l: any, index) => ({ key: index, ...l }))
    : [];

  const dataColumns: any = columns
    ? columns.map(
        ({
          name,
          type,
          file,
        }: {
          name: string;
          type: string;
          file: boolean;
        }) => ({
          title: name.toString(),
          dataIndex: name.toString(),
          key: name.toString(),
          type,
          file,
        })
      )
    : [];

  dataColumns.push({
    title: "Action",
    key: "operation",
    fixed: "right",
    width: 50,
    type: "operation",
  });

  const handleTableChange = (pagination: any, filters: any, sorter: any) => {
    setPagination(pagination);
    getCollectionList(collectionName, pagination);
  };

  if (!collectionName)
    return (
      <>
        <PageHeader
          title="Available Collections"
          subTitle="Select any collection to view data"
          ghost={false}
        />
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            padding: 20,
            width: "100%",
          }}
        >
          <List
            dataSource={collections}
            renderItem={(collection) => (
              <Tag
                style={{
                  paddingTop: 15,
                  paddingBottom: 15,
                  textTransform: "capitalize",
                  margin: 5,
                  letterSpacing: 1.2,
                  cursor: "pointer",
                  alignContent: "center",
                  paddingLeft: 30,
                  paddingRight: 30,
                  fontWeight: 600,
                  fontSize: 20,
                }}
                color="orange"
                onClick={() => {
                  setSelectedCollection(collection);
                  history.push(`/data/${collection.name}`);
                }}
              >
                <span>{collection.name}</span>
              </Tag>
            )}
          />
        </div>
      </>
    );

  if (params.action && params.action === "create" && selectedCollection)
    return (
      <CreateCollectionAction
        selectedCollection={selectedCollection}
        action="create"
      />
    );
  if (params.action && params.action === "update" && selectedCollection)
    return (
      <CreateCollectionAction
        selectedCollection={selectedCollection}
        action="update"
        id={params.id}
        selectedRecord={selectedRecord}
      />
    );

  if (params.action && params.action === "show" && selectedCollection)
    return (
      <ShowCollectionAction
        selectedCollection={selectedCollection}
        action="show"
        id={params.id}
        selectedRecord={selectedRecord}
      />
    );

  return (
    <>
      <PageHeader
        style={{ marginTop: 2 }}
        ghost={false}
        onBack={() => window.history.back()}
        title={
          <Space>
            <span style={{ textTransform: "capitalize" }}>
              {collectionName}{" "}
            </span>
            {pagination?.total && (
              <Tag color="geekblue">{pagination.total}</Tag>
            )}
          </Space>
        }
        extra={[
          rowSelection.length ? (
            <Button
              onClick={() => {
                return confirm({
                  title: "Sure you want to delete selected records?",
                  icon: <ExclamationCircleOutlined />,
                  content: null,
                  onOk() {
                    deleteManyRecord();
                  },
                  onCancel() {
                    console.log("Cancel");
                  },
                });
              }}
            >
              <DeleteOutlined />
              Delete
            </Button>
          ) : null,
          <Button key="2">
            <FilterOutlined />
            Filter
          </Button>,
          <Button
            key="1"
            type="primary"
            onClick={() => {
              history.push(`/data/${collectionName}?action=create`);
            }}
          >
            <PlusOutlined />
            Create
          </Button>,
        ]}
      ></PageHeader>
      <Table
        loading={loading}
        style={{ padding: 10 }}
        dataSource={dataSource}
        size="middle"
        rowKey={(record) => record._id}
        rowSelection={{
          selectedRowKeys: rowSelection,
          onChange: (selectedRows) => {
            setRowSelection(selectedRows);
          },
        }}
        pagination={pagination}
        onChange={handleTableChange}
      >
        {dataColumns.map((column: any) => {
          switch (column.type) {
            case "objectId":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  width={70}
                  dataIndex={column.dataIndex}
                  key={column.key}
                />
              );
            case "boolean":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text, record, index) => {
                    if (text === undefined) return null;
                    return (
                      <>
                        {text ? (
                          <Tag
                            color={"green"}
                            style={{ textTransform: "capitalize" }}
                          >
                            {text.toString()}
                          </Tag>
                        ) : (
                          <Tag
                            color={"red"}
                            style={{ textTransform: "capitalize" }}
                          >
                            {text.toString()}
                          </Tag>
                        )}
                      </>
                    );
                  }}
                />
              );

            case "date":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text: Date) => {
                    return text ? (
                      <span>{moment(text).format("MM/DD/YYYY hh:mm")}</span>
                    ) : null;
                  }}
                />
              );

            case "mixed":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      {column.title}

                      <Tag color="magenta">JSON</Tag>
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text: any) => {
                    return text ? (
                      <span>
                        {Object.keys(text).map((key) => (
                          <Tag>{key}</Tag>
                        ))}
                      </span>
                    ) : null;
                  }}
                />
              );
            case "operation":
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  width={60}
                  render={(text, record: any) => {
                    return (
                      <Dropdown
                        overlay={
                          <Menu>
                            <Menu.Item>
                              <Button
                                style={{ width: "100%" }}
                                className="button-blue"
                                icon={<EyeOutlined />}
                                onClick={() => {
                                  setSelectedRecord(record);
                                  history.push(
                                    `/data/${collectionName}?action=show&id=${record._id}`
                                  );
                                }}
                              >
                                View
                              </Button>
                            </Menu.Item>
                            <Menu.Item>
                              <Button
                                style={{ width: "100%" }}
                                className="button-green"
                                icon={<EditOutlined />}
                                onClick={() => {
                                  setSelectedRecord(record);
                                  history.push(
                                    `/data/${collectionName}?action=update&id=${record._id}`
                                  );
                                }}
                              >
                                Edit
                              </Button>
                            </Menu.Item>
                            <Menu.Item>
                              <Button
                                icon={<DeleteOutlined />}
                                className="button-red"
                                onClick={() => {
                                  return confirm({
                                    title:
                                      "Sure you want to delete this record?",
                                    icon: <ExclamationCircleOutlined />,
                                    content: null,
                                    onOk() {
                                      deleteRecord(record._id);
                                    },
                                    onCancel() {
                                      console.log("Cancel");
                                    },
                                  });
                                }}
                              >
                                Delete
                              </Button>
                            </Menu.Item>
                          </Menu>
                        }
                        placement="bottomLeft"
                      >
                        <MoreOutlined
                          style={{ cursor: "pointer", maxHeight: 10 }}
                        />
                      </Dropdown>
                    );
                  }}
                />
              );
            case "hasone":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      {column.title}

                      {column.file && <Tag color="cyan">File</Tag>}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text) => {
                    return (
                      text && (
                        <Space direction="vertical">
                          {column.file ? (
                            <Space>
                              <Image
                                preview={false}
                                width={100}
                                height={100}
                                src={"/admin/file/get/" + text.filename}
                                fallback={"/img/file.svg"}
                              />

                              <span
                                style={{
                                  color: "GrayText",
                                  display: "flex",
                                  justifyContent: "space-between",
                                  flexDirection: "column",
                                }}
                              >
                                <b>{text.originalname}</b>
                                {prettySize(text.size)} {text.mimetype}
                              </span>
                            </Space>
                          ) : typeof text === "string" ? (
                            <span>{text.slice(0, 200)} </span>
                          ) : (
                            Object.values(text).map((value, index) => {
                              if (!(typeof value === "string")) return null;
                              if (index === 1) return <span>{value} </span>;
                              return null;
                            })
                          )}
                        </Space>
                      )
                    );
                  }}
                />
              );
            case "hasmany":
              return (
                <Column
                  title={
                    <span
                      style={{
                        textTransform: "capitalize",
                        display: "flex",
                        justifyContent: "space-around",
                      }}
                    >
                      {column.title}

                      {column.file && <Tag color="cyan">Files</Tag>}
                    </span>
                  }
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text) => {
                    return (
                      <span>
                        {text?.length} {column.title}
                      </span>
                    );
                  }}
                ></Column>
              );
            default:
              return (
                <Column
                  title={
                    <span style={{ textTransform: "capitalize" }}>
                      {column.title}
                    </span>
                  }
                  width={100}
                  dataIndex={column.dataIndex}
                  key={column.key}
                  render={(text) => {
                    return (
                      <span>
                        {text.toString().slice(0, 80)}
                        {text.toString().length > 80 ? "..." : null}
                      </span>
                    );
                  }}
                />
              );
          }
        })}
      </Table>
    </>
  );
}

export default DataFlowPage;
