import React, { useState, useEffect } from "react";
import {
  Row,
  Col,
  List,
  Button,
  Space,
  Card,
  Tag,
  Layout,
  Input,
  PageHeader,
  message,
} from "antd";
import { DeleteOutlined, EditOutlined } from "@ant-design/icons";

import Badge from "../components/Badge";

import { useSelector, shallowEqual, useDispatch } from "react-redux";
import { Dispatch } from "redux";
import {
  selectCollection,
  updateCollection,
  removeCollection,
} from "../store/actionCreators";

import NewCollectionModal from "../components/NewCollectionModal";
import EditCollectionModal from "../components/NewCollectionModal";
import NewCollectionTypeModal from "../components/NewCollectionTypeModal";
import RemoveCollectionTypeModal from "../components/RemoveCollectionTypeModal";
import RemoveCollectionModal from "../components/RemoveCollectionModal";

import {
  ICollection,
  CollectionsState,
  ICollectionSchema,
} from "../types/global";

// define vars
const { Sider, Content } = Layout;

function CreateSchema() {
  const [editSchema, setEditSchema] = useState<ICollectionSchema>();
  const [openCollectionModal, setOpenCollectionModal] = useState(false);
  const [editCollectionModal, setEditCollectionModal] = useState(false);
  const collections: ICollection[] = useSelector(
    (state: CollectionsState) => state.collections
  );

  const [localCollections, setLocalCollections] = useState<Array<ICollection>>(
    []
  );

  const selectedCollection: ICollection | undefined = useSelector(
    (state: CollectionsState) => state.selectedCollection,
    shallowEqual
  );

  const dispatch: Dispatch<any> = useDispatch();

  const setSelectCollection = React.useCallback(
    (collection: ICollection) => dispatch(selectCollection(collection)),
    [dispatch]
  );

  const removeCollectionType = React.useCallback(
    (collection: ICollection) => dispatch(updateCollection(collection)),
    [dispatch]
  );

  const saveCollection: any = React.useCallback(
    (collection: ICollection) => dispatch(updateCollection(collection)),
    [dispatch]
  );

  const deleteCollectionAction = React.useCallback(
    (collection: ICollection) => dispatch(removeCollection(collection)),
    [dispatch]
  );

  useEffect(() => {
    if (!selectedCollection && collections.length) {
      setSelectCollection(collections[0]);
    }
    setLocalCollections(collections);
  }, [collections]);

  return (
    <>
      <Layout style={{ height: "100%" }}>
        <Sider
          width="400"
          theme="light"
          style={{
            backgroundColor: "transparent",
          }}
        >
          <PageHeader
            title="Collection List"
            extra={[
              <Tag color="green">
                Total {collections.length}
              </Tag>,
              <NewCollectionModal
                collections={collections}
                isModalVisible={openCollectionModal}
                setIsModalVisible={setOpenCollectionModal}
              />,
            ]}
          />

          <div style={{ padding: 10 }}>
            <Input
              size="large"
              placeholder="Search Collection"
              onChange={(e) => {
                let searchCollections: Array<ICollection> = [];

                if (e.target.value.length) {
                  searchCollections = collections.filter(
                    (collection: ICollection) =>
                      collection.name
                        .trim()
                        .toLowerCase()
                        .includes(e.target.value.toLowerCase())
                  );
                  setLocalCollections(searchCollections);
                } else {
                  setLocalCollections(collections);
                }
              }}
            />
          </div>

          <List
            grid={{
              gutter: 10,
              xs: 1,
              sm: 1,
              md: 1,
              lg: 1,
              xl: 1,
              xxl: 1,
            }}
            dataSource={localCollections}
            style={{ padding: 10, height: "80vh", overflow: "auto" }}
            renderItem={(collection, index) => (
              <List.Item key={index}>
                <Tag
                  key={index}
                  style={{
                    textTransform: "capitalize",
                    cursor: "pointer",
                    height: 55,
                    width: "100%",
                    display: "grid",
                    placeItems: "left",
                    fontSize: "20px",
                    letterSpacing: 1.3,
                    padding: 10
                  }}
                  color={
                    collection.name === selectedCollection?.name
                      ? "orange"
                      : collection.show ? "default" : "error"
                  }
                  onClick={() => {
                    setSelectCollection(collection);
                  }}
                >
                  {collection.name}

                  <span style={{ fontSize: 12 }}>
                    {collection.fields} fields
                    <span style={{ fontSize: 12, marginLeft: 10 }}>
                      {collection.show ? null : "Hidden"}
                    </span>
                  </span>
                </Tag>
              </List.Item>
            )}
          />
        </Sider>

        {selectedCollection && (
          <Layout style={{ backgroundColor: "#fafafb", height: "100%" }}>
            <Content>
              <PageHeader
                title={
                  <span style={{ textTransform: "capitalize" }}>
                    {selectedCollection.name}
                  </span>
                }
                extra={selectedCollection.show ? [
                  <Space>
                    < EditCollectionModal collections={collections}

                      isModalVisible={editCollectionModal}
                      setIsModalVisible={setEditCollectionModal}
                      edit={true}
                      selectedCollection={selectedCollection}

                    />
                    <Button
                      className="button-green"

                      onClick={() => {
                        saveCollection(selectedCollection);
                        message.success("Collection Saved");
                      }}
                    >
                      Save
                    </Button>
                    <Button
                      className="button-red"
                      onClick={() => {
                        RemoveCollectionModal({
                          deleteCollectionAction: deleteCollectionAction,
                          collection: selectedCollection,
                        });
                      }}
                    >
                      Delete
                    </Button>
                  </Space>,
                ] : [
                  <Tag color="geekblue" >Hidden</Tag>
                ]}
              />

              <Row>
                <Col span={24} style={{ paddingLeft: 20 }}>
                  <h5>Allowed Operations</h5>
                  {Object.keys(selectedCollection.operations).map(operation => <Tag color={selectedCollection.operations[operation] ? "geekblue" : "volcano"} >{operation}</Tag>)}
                </Col>
              </Row>

              <Row>
                <Col span={24} style={{ padding: 20 }}>
                  <Card
                    bordered={false}
                    style={{ boxShadow: "rgb(227 233 243) 0px 2px 4px" }}
                    title={
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "space-between",
                        }}
                      >
                        <h1>{`${selectedCollection.fields} fields`} </h1>
                      </div>
                    }
                    bodyStyle={{ padding: 0 }}
                  >
                    {Object.keys(selectedCollection.schema).map(
                      (key: any, index) => {
                        const schema: ICollectionSchema =
                          selectedCollection.schema[key];
                        const { type } = schema;
                        return (
                          <Row
                            gutter={0}
                            style={{
                              height: 60,
                              padding: 15,
                              borderBottom: "1px solid #f0f0f0",
                            }}
                            align="middle"
                            key={index}
                          >
                            <Col span={6}>
                              <Badge type={type} />
                            </Col>
                            <Col span={6}>
                              <span
                                style={{
                                  fontSize: 15,
                                  color: "#141414",
                                  fontWeight: 500,
                                }}
                              >
                                {key}
                              </span>{" "}
                            </Col>
                            <Col span={6}>
                              <span
                                style={{
                                  fontSize: 15,
                                  color: "#141414",
                                  textTransform: "capitalize",
                                }}
                              >
                                {type}
                              </span>{" "}
                            </Col>
                            <Col
                              span={6}
                              style={{
                                display: "flex",
                                justifyContent: "flex-end",
                              }}
                            >
                              {selectedCollection.show && <Space size={25}>
                                <EditOutlined
                                  style={{ color: "#8c8c8c", fontSize: 20 }}
                                  onClick={() =>
                                    setEditSchema({ ...schema, name: key })
                                  }
                                />

                                <DeleteOutlined
                                  style={{ color: "#cf1322", fontSize: 20 }}
                                  className="pointer"
                                  onClick={() =>
                                    RemoveCollectionTypeModal({
                                      removeCollectionType,
                                      collection: selectedCollection,
                                      key,
                                    })
                                  }
                                />
                              </Space>}
                            </Col>
                          </Row>
                        );
                      }
                    )}

                    {selectedCollection.show && <NewCollectionTypeModal
                      selectedCollection={selectedCollection}
                      editSchema={editSchema}
                    />}
                  </Card>
                </Col>
              </Row>
            </Content>
          </Layout>
        )}
      </Layout>
    </>
  );
}

export default CreateSchema;
